<template>
  <div class="ablums">
    <div id="perspective">
      <div id="wrap" @click="clickPhoto">
        <img src="./img/1.jpg" />
        <img src="./img/2.jpg" />
        <img src="./img/3.jpg" />
        <img src="./img/4.jpg" />
        <img src="./img/5.jpg" />
        <img src="./img/6.jpg" />
        <img src="./img/7.jpg" />
        <img src="./img/8.jpg" />
        <img src="./img/9.jpg" />
        <img src="./img/10.jpg" />
        <img src="./img/11.jpg" />
        <img src="./img/12.jpg" />
        <img
          src="https://imgservice.suning.cn/uimg1/b2c/image/z4n_xBLQggr1UJszNhVHfQ.jpg_800w_800h_4e_80Q_is"
        />
        <p></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  components: {},
  methods: {
    clickPhoto() {
      var oWrap = document.querySelector("#perspective #wrap");
      var oImg = oWrap.getElementsByTagName("img");
      var oImgLength = oImg.length;
      var Deg = 360 / oImgLength;
      for (var i = 0; i < oImgLength; i++) {
        oImg[i].style.transform =
          "rotateY(" + i * Deg + "deg) translateZ(150px)";
        oImg[i].style.transition =
          "transform 1s " + (oImgLength - 1 - i) * 0.1 + "s";
      }
    },
  },
  created() {
    this.$nextTick(() => {
      var oWrap = document.querySelector("#perspective #wrap");
      var oImg = oWrap.getElementsByTagName("img");
      var oImgLength = oImg.length;
      var Deg = 360 / oImgLength;
      var nowX,
        nowY,
        lastX,
        lastY,
        minusX = 0,
        minusY = 0;
      var roY = 0,
        roX = -10;
      var timer;
      this.clickPhoto();

      // for (var i = 0; i < oImgLength; i++) {
      //   oImg[i].style.transform =
      //     "rotateY(" + i * Deg + "deg) translateZ(350px)";
      //   oImg[i].style.transition =
      //     "transform 1s " + (oImgLength - 1 - i) * 0.1 + "s";
      // }

      // mTop();

      // window.onresize = mTop;

      // function mTop() {
      //   var wH = document.documentElement.clientHeight;
      //   oWrap.style.marginTop = wH / 2 - 180 + "px";
      // }

      const perpectiveDom = document.querySelector("#perspective");
      // 拖拽：三个事件-按下 移动 抬起
      //按下
      perpectiveDom.ontouchstart = perpectiveDom.onmousedown = function(ev) {
        ev = ev || window.event;

        //鼠标按下的时候，给前一点坐标赋值，为了避免第一次相减的时候出错
        lastX = ev.clientX || ev.changedTouches[0].clientX;
        lastY = ev.clientY || ev.changedTouches[0].clientY;

        //移动
        this.ontouchmove = this.onmousemove = function(moveev) {
          ev = ev || window.event;

          clearInterval(timer);

          nowX = ev.clientX || moveev.changedTouches[0].clientX; // clientX 鼠标距离页面左边的距离
          nowY = ev.clientY || moveev.changedTouches[0].clientY; // clientY ………………………………顶部………………

          //当前坐标和前一点坐标差值
          minusX = nowX - lastX;
          minusY = nowY - lastY;

          //更新wrap的旋转角度，拖拽越快-> minus变化大 -> roY变化大 -> 旋转快
          roY += minusX * 0.2; // roY = roY + minusX*0.2;
          roX -= minusY * 0.1;

          oWrap.style.transform =
            "rotateX(" + roX + "deg) rotateY(" + roY + "deg)";

          /*
      //生成div，让div跟着鼠标动
      var oDiv = document.createElement('div');
      oDiv.style.cssText = 'width:5px;height:5px;background:red;position:fixed;left:'+nowX+'px;top:'+nowY+'px';
      this.body.appendChild(oDiv);
      */

          //前一点的坐标
          lastX = nowX;
          lastY = nowY;
        };
        //抬起
        this.ontouchend = this.onmouseup = function() {
          this.onmousemove = null;
          this.ontouchend = null;
          timer = setInterval(function() {
            minusX *= 0.95;
            minusY *= 0.95;
            roY += minusX * 0.2; // roY = roY + minusX*0.2;
            roX -= minusY * 0.1;
            oWrap.style.transform =
              "rotateX(" + roX + "deg) rotateY(" + roY + "deg)";

            if (Math.abs(minusX) < 0.1 && Math.abs(minusY) < 0.1) {
              clearInterval(timer);
            }
          }, 13);
        };
        return false;
      };
    });
  },
};
</script>

<style type="text/css" lang="less">
