  content: "";
  display: block;
  position: absolute;
  inset: -100px -200px;
  width:100vw;
  // background: red;
}

#wrap {
  width: 60px;
  /*133:200  4:6  */
  height: 90px;
  margin: 0 auto;
  position: relative;

  /*搭建3D效果必须的两个属性：一个变换风格变3d，一个场景景深800px*/
  transform-style: preserve-3d;
  transform: rotateX(-10deg) rotateY(0deg);
}

#wrap img {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 0.5px;

  transform: rotateY(0deg) translateZ(0px);
  /*倒影：朝向 偏移 遮盖*/
  /*线性渐变(从哪里开始,开始时候的颜色,结束时候的颜色)*/
  -webkit-box-reflect: below 2.5px -webkit-linear-gradient(top, rgba(0, 0, 0, 0)
        40%, rgba(0, 0, 0, 0.5) 100%);
}

#wrap p {
  width: 600px;
